			"title": "Ise 3",
			"description": "A unique hybrid ship that combines the features of a battleship and an aircraft carrier. Initially built as a version of the preceding Fus class, Ise underwent extensive reconstruction in the 1940s. Some of the ship's main battery turrets were replaced by an aircraft hangar, surmounted by a flight deck.",
