			"title": "Bungo",
			"description": "After revising the development plan of the Japanese Navy, new battleships and battlecruisers were ordered. This is how ProjectL emerged, being an expanded version of ProjectA, which became the Kiiclass. She also had five main battery turrets housing 457mm guns. However, the project's displacement exceed that of the Kiiclass by 9,000tons, resulting in a cost surge. The emergence of both the Kiiclass and the mentioned ships was prevented by the Washington Naval Conference, which resulted in a temporary ban on the construction of battleships and limited their total tonnage.",
