			"title": "Hakury",
			"description": "The G-15 project, or the Improved Taih, had larger dimensions, an enhanced armor layout, and an increased air group compared to her predecessor. It was planned to build a series of seven ships, and the construction process was scheduled to end in 1948. After the Battle of Midway, these plans were suspended in favor of the Unry-type aircraft carriers with a simpler design, and they were eventually entirely abandoned in 1943.",
