			"title": "Flint",
			"description": "USS Flint belonged to the improved Atlanta subclass, also known as the Oakland class. During World WarII, this ship provided cover for aircraft carriers during the landing on Luzon as well as air raids on Taiwan, Tokyo, and Kysh. She also provided anti-aircraft support for U.S.Marines during the Battle of IwoJima. After the end of World WarII, USSFlint transported service members from the Pacific Ocean to the U.S.",
