			"title": "AL Montpelier",
			"description": "Brave and energetic like her elder sister, Cleveland. Loves battles and stands up to even the strongest of foes, but is clumsy at everything other than combat. Member of Task Force 18a group assembled of ships of her type. Admires her sister, Cleveland.\nFrom the Azur Lane game.",
