			"title": "Anchorage",
			"description": "Historically, U.S. Navy heavy cruisers were not armed with torpedo tubesthis put them in a vulnerable position, particularly when compared to Japanese cruisers equipped with 610mm torpedoes. Had the CA-B heavy cruisers evolved to be equipped with torpedo armament, such a ship could have been armed with two 533mm quadruple-tube torpedo launchersone on each side.",
