			"title": "KearsargeB",
			"description": "In the 1930s, several variants of large artillery ships were designed to reinforce the Soviet fleet. The U.S.S.R also studied the American experience of building such ships. Three hybrid battleship-carrier projects were developed in the U.S. on commercial orders issued by the U.S.S.R. One of them was armed with twelve 406mm guns in four turrets. However, the Soviet side later recognized the inexpediency of building such large and expensive ships, and the project was halted.",
