			"description": "A hypothetical armament project for the Montana-class battleship with eight 457mm guns housed in four turrets. Back in1938, amid rumors about the creation of ships with guns of a caliber exceeding 406mm in Japan, there were projects of battleships with 457mm guns. One of them was relatively fast and had armament comprising six guns in three twin-gun turrets. The twin-gun turrets developed for this project could well have been mounted on one of the unfinished Montana-class battleships.",
