			"description": "USS United States was designed as the flagship of a series of five attack aircraft carriers of a new class. These ships were intended to operate jet bombers, and their flight deck was structurally integrated into their load-bearing hull structure for this purpose. USSUnited States was intended to operate as part of a task force alongside conventional strike aircraft carriers that would provide air cover for the task force. The ship was laid down at the Newport News shipyard, but her construction was abruptly halted five days later.",
