			"title": "Derzki",
			"description": "A competition was announced in1911 to design a 34-knot destroyer for the Black Sea Fleet. The basis for the performance specifications was Novik, the most modern destroyer at that time, which was under construction inGermany. The winner was the project by the Putilov Plant. The construction of the first two destroyers began in1912. A year later, another two ships were laid down to add to those already launched. During World WarI, Derzki engaged in raiding, patrolling, and escorting operations.",
