			"title": "Izyaslav",
			"description": "In the run-up to World WarI, international tensions motivated the adoption of an intense shipbuilding program. A proposition was made to build 36destroyers for the Baltic Fleet. The third of five series comprised the largest and the most heavily armed Izyaslav class destroyers. The lead ship took part in the Battle of Moon Sound and in the Ice Cruise of the Baltic Fleet. In1922, she was renamed Karl Marx.",
