			"description": "In 1928, a design of a 40-knot destroyer for the Black Sea Fleet was developed. General arrangement drawings were approved in1929. The construction of the planned ships never started due to cuts in the navy's shipbuilding budget. Later, Leningrad-class leaders were laid down and built to an improved design (Project1).",
