			"description": "In the summer of1932, a Soviet Navy delegation visited Italy to see the latest Maestrale-class destroyers. This class was selected as the basis for new Soviet destroyer designs. The technical design was assigned project number7 and was approved in December1934. Laid down at Shipyard No.190 inLeningrad in1935, Gnevny was commissioned in October1938. On June23, 1941, the destroyer was sunk by a German mine barrier.",
