			"title": "Borodino",
			"description": "While designing the Project82 heavy cruiser, the Ministry of Shipbuilding Industry of the U.S.S.R. ordered drafts of a \"small battleship\"a ship equipped with 12 turrets housing main- and medium-caliber guns and enhanced bow armor. In19511952, nine preliminary draft projects were prepared. One of the projects was armed with two triple turrets with 406 mm guns and was recommended for further development. After Stalin's death, the construction of heavy artillery ships was no longer relevant, and the project wasn't completed.",
