			"title": "Admiral Ushakov",
			"description": "In 1945, the Navy's General Staff gave the order to develop a battleship under Project24, which included 15draft designs within its framework to be reviewed. The largest of them was a project with a suggested displacement of 110,000tons that was similar in architecture to the Project82 heavy cruisers. Plans were made to construct the ship by1955, but they never came to fruition. If she had ended up being embodied in metal, the ship could have become the largest and most powerful battleship in history.",
