			"description": "The first project for submarines designed specifically to stay most of the time underwater was ProjectXXI. They were equipped with improved batteries and a snorkela device that enabled the use of diesel engines for underwater movement. These submarines were also equipped with low-noise electric motors, allowing them to stealthily approach their targets. The lead submarine of the series, U-2501, did not participate in combat patrols and was sunk in Hamburg on May3, 1945.",
