			"title": "Hindenburg",
			"description": "A further development of the Admiral Hipper-class cruisers, unconstrained by the Washington Naval Treaty restrictions, could have been ships with four 203mm triple-gun turrets. Such armament would have made the new German cruisers the strongest in their ship type right until the appearance of American ships of the DesMoines class with their rapid-firing mounts. However, priorities in shipbuilding shifted to submarines, and such a ship was never built inGermany.",
