			"title": "Pommern B",
			"description": "When designing the H-class battleship, the main requirement was for her firepower to surpass that of all counterparts anywhere in the world. Despite Germany's pledge to adhere to the Washington Naval Treaty, the nation's leadership insisted on using the 420mm caliber. Alternatives included using the 406mm caliber (which would have taken much longer for Germany to develop) or increasing the number of 380mm guns to twelve (placed in four triple-gun turrets).",
