			"description": "A project of a high-speed battleship with four main battery turrets and a displacement of 50,000t. She was 30m longer than her predecessor, making her the largest among the draft designs conceived by the Construction Office of the German Navy Headquarters in1918. This project represents the largest and most heavily armed of all German battleships, extending up until the elaborations of the early 1940s relating to the H-class battleship.",
