			"title": "Hannover",
			"description": "A further development of the H-class project with reinforced main battery guns. The next step after the 457mm guns would be to increase the caliber to 19inches (483mm). Such a caliber would establish German battleships as the most heavily armed vessels in the annals of naval history. By the time of their development, plans were in place to completely discontinue the use of non-dual-purpose secondary battery guns and replace them with dual-purpose artillery.",
