			"title": "Cheshire",
			"description": "In1940, a project for a 15,500-ton cruiser armed with 203mm guns was considered. It was believed that this caliber should suffice for performing combat missions. However, a year prior to that, 234mm and 254mm guns were mentioned in projects. A compromise between the lower cost of the 15,500-ton cruiser and 234mm guns would have involved installing twin-gun main battery turrets on such a cruiser.",
