			"title": "Marseille",
			"description": "In the process of developing a battlecruiser or \"large\" cruiser in the late 1920s and early 1930s, several cruiser projects with a displacement of 23,690tons were created. One of the projects involved a ship armed with nine 330mm guns in three triple-gun turrets on the ship's bow, with the second turret being elevated.",
