			"title": "Turenne",
			"description": "Several projects were approved under the naval program of1906, which provided for the construction of nineteen battleships. The final version incorporated the slightly enlarged hull of squadron battleship Danton with a linear-rhombic arrangement of six 305mm twin-gun turrets. The armor and propulsion unit also matched the prototype. While the project was never implemented, its further development led to the appearance of the Courbet-class battleships.",
