			"title": "Strasbourg",
			"description": "The second Dunkerque-class battlecruiser featured an improved armor layout, including a thicker armor belt. At the beginning of World WarII, Strasbourg participated in patrol and escort operations before being sent to Mers-el-Kbir. After the capitulation of France, the British Navy attacked the French ships as part of Operation Catapult. Strasbourg managed to escape the harbor and arrive at Toulon.",
