			"title": "Picardie",
			"description": "In 1912, France initiated the construction of larger docks at shipyards in Toulon and Brittany. By that time, the major naval powers were laying down ships significantly more advanced than their predecessors in terms of main caliber. France was also contemplating a shift toward a caliber of 370380 mm; however, naval guns of this caliber were not yet accessible in France. As a solution, engineers opted to increase the number of smaller-caliber guns, with the final design featuring as many as 16 guns placed in four turrets.",
