			"description": "Between 1939 and 1941, a number of draft designs of a \"long-range small light cruiser\" were developed. This ship was meant to be capable of fighting enemy destroyers and leaders, spearheading on its own, mining enemy waters, attacking communication lines, and supporting landing operations. The drafts included a project for a cruiser with the MK-6 number, armed with twin mounts housing dual-purpose 130mm guns. If the ships of this class had actually been built, some of them might have been transferred to the People's Republic ofChina Navy along with Project7 destroyers and coastguards.",
