			"title": "Vittorio Cuniberti",
			"description": "After the Maestrale-class destroyers entered service, the development of an improved design began. One of the variants featured a third 120mm twin gun mount placed in a superfiring position on the ship's bow. The ship's displacement increased to about 2,000tons. The project was considered too expensive compared to Oriani- and Soldati-class destroyers that were already under construction, so it was never implemented.",
