			"title": "Ruggiero diLauria",
			"description": "In 1921, the engineer Ferdinando Cassone introduced the concept of a battlecruiser equipped with eight 457mm guns and a speed of 3540knotsthe largest ship ever designed for the Italian Navy. To reduce her overall length, it was suggested to place the boilers above the engines in a two-tier propulsion unit. Besides that, she had unique anti-torpedo protection planned: Any detonation was meant to be directed to the overboard expansion chamber, from where the gases would be discharged through special channels. The project was ahead of its time and was never implemented.",
