			"title": "Hrcules",
			"description": "British River-class frigates, designed to escort convoys in the North Atlantic, were sold off heavily after World WarII. USSAsheville, a Canadian-built ship, remained in the U.S. Navy until1946 but was later sold toArgentina. There, she served under the name \"Hrcules\" and then \"Juan B. Azopardo\" until1973.",
