			"title": "Ignacio Allende",
			"description": "In 1947, the Inter-American Treaty of Reciprocal Assistance was signed in Rio deJaneiro, whereby Argentina, Brazil, and Chile were to strengthen their fleets by purchasing old U.S. Brooklyn-class cruisers. If those countries had ordered the construction of new cruisers in the United States, then the most modern Worcester-class ships would have been used as the basis for the projects.",
